import { FormGroup, FormControlLabel, Switch } from "@mui/material";

function SudokuConfigForm(props) {
	const { configData, setConfigData } = props;

	const handleClick = (e) => {
		setConfigData({
			...configData,
			[e.target.name]: e.target.checked,
		});
	};

	return (
		<FormGroup row>
			<FormControlLabel
				control={
					<Switch checked={configData.answerMode} onChange={handleClick} name="answerMode" color="primary" />
				}
				label="Answer Mode"
			/>
			<FormControlLabel
				control={
					<Switch
						checked={configData.solveItself}
						onChange={handleClick}
						name="solveItself"
						color="primary"
					/>
				}
				label="Solve Itself"
			/>
		</FormGroup>
	);
}

export default SudokuConfigForm;
export { SudokuConfigForm };
