import "./index.css";

function DigitCell(props) {
	const { children, selected, answer, highlighted, ...rest } = props;

	return (
		<td
			className={`${
				selected ? "digit-cell-selected" : highlighted ? "digit-cell-highlighted" : "digit-cell-normal"
			} ${answer ? "digit-cell-answer" : ""}`}
			{...rest}
		>
			{children}
		</td>
	);
}

export default DigitCell;
export { DigitCell };
