import { useState, useEffect } from "react";
import "./index.css";
import { SudokuConfigForm } from "../SudokuConfigForm";
import { SudokuActionSection } from "../SudokuActionSection";
import { DigitCell } from "../DigitCell";
import { SudokuSolver } from "../../utils";
import { getNonetIndexByRowCol } from "../../utils/SudokuUtils";

const SAMPLE_SETUP = [
	[3, null, null, null, 6, null, null, null, 4],
	[null, 9, null, null, 2, null, null, 1, null],
	[null, null, 2, null, null, null, 8, null, null],
	[null, null, null, 8, null, 2, null, null, null],
	[4, 5, null, null, null, null, null, 3, 7],
	[null, null, null, 3, null, 5, null, null, null],
	[null, null, 5, null, null, null, 7, null, null],
	[null, 1, null, null, 3, null, null, 9, null],
	[7, null, null, null, 1, null, null, null, 2],
];

// Array(9)
// 	.fill(null)
// 	.map((e, i) => Array(9).fill(null));

function Sudoku(props) {
	const [configData, setConfigData] = useState({
		answerMode: false,
		solveItself: false,
	});
	const [sudokuQuestionCellValues, setSudokuQuestionCellValues] = useState(SAMPLE_SETUP);
	const [sudokuAnswerCellValues, setSudokuAnswerCellValues] = useState(JSON.parse(JSON.stringify(SAMPLE_SETUP)));
	const [sudokuPossibleAnswerCellValues, setSudokuPossibleAnswerCellValues] = useState(
		JSON.parse(JSON.stringify(SAMPLE_SETUP)).map((e) =>
			e.map((o) =>
				o
					? []
					: Array(9)
							.fill(null)
							.map((e, i) => i + 1)
			)
		)
	);
	const [selectedRow, setSelectedRow] = useState(0);
	const [selectedCol, setSelectedCol] = useState(0);

	useEffect(() => {
		setSudokuPossibleAnswerCellValues(
			JSON.parse(JSON.stringify(sudokuQuestionCellValues)).map((e) =>
				e.map((o) =>
					o
						? []
						: Array(9)
								.fill(null)
								.map((e, i) => i + 1)
				)
			)
		);
	}, [configData.solveItself, sudokuQuestionCellValues]);

	useEffect(() => {
		if (configData.solveItself) {
			setSudokuPossibleAnswerCellValues(
				SudokuSolver.solveSudoku(
					sudokuAnswerCellValues,
					JSON.parse(JSON.stringify(sudokuPossibleAnswerCellValues))
				)
			);
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [configData.solveItself, sudokuAnswerCellValues]);

	useEffect(() => {
		for (let row = 0; row < 9; row++) {
			for (let col = 0; col < 9; col++) {
				if (sudokuPossibleAnswerCellValues[row][col].length === 1) {
					setSudokuAnswerCellValues(
						getNewMultiDimensionalArrayWithUpdatedValue(
							sudokuAnswerCellValues,
							sudokuPossibleAnswerCellValues[row][col][0],
							row,
							col
						)
					);
				}
			}
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [sudokuPossibleAnswerCellValues]);

	const getNewMultiDimensionalArrayWithUpdatedValue = (mdArr, newValue, ...dimensions) =>
		updateMultiDimensionalArray(JSON.parse(JSON.stringify(mdArr)), newValue, ...dimensions);

	const updateMultiDimensionalArray = (mdArr, newValue, ...dimensions) => {
		let arrRef = mdArr;
		for (let i = 0; i < dimensions.length - 1; i++) {
			arrRef = arrRef[dimensions[i]];
		}
		arrRef[dimensions[dimensions.length - 1]] = newValue;

		return mdArr;
	};

	const sampleQuestionAction = () => {
		setConfigData({ ...configData, answerMode: false, solveItself: false });
		setSudokuQuestionCellValues(SAMPLE_SETUP);
		setSudokuAnswerCellValues(JSON.parse(JSON.stringify(SAMPLE_SETUP)));
		setSudokuPossibleAnswerCellValues(
			JSON.parse(JSON.stringify(SAMPLE_SETUP)).map((e) =>
				e.map((o) =>
					o
						? []
						: Array(9)
								.fill(null)
								.map((e, i) => i + 1)
				)
			)
		);
		setSelectedRow(4);
		setSelectedCol(4);
	};
	const clearQuestionAction = () => {
		setConfigData({ ...configData, answerMode: false, solveItself: false });
		setSudokuQuestionCellValues(
			Array(9)
				.fill(null)
				.map((e, i) => Array(9).fill(null))
		);
		setSudokuAnswerCellValues(
			Array(9)
				.fill(null)
				.map((e, i) => Array(9).fill(null))
		);
	};
	const clearAnswerAction = () => {
		setConfigData({ ...configData, solveItself: false });
		setSudokuAnswerCellValues(JSON.parse(JSON.stringify(sudokuQuestionCellValues)));
	};

	return (
		<>
			<SudokuConfigForm configData={configData} setConfigData={setConfigData} />

			<SudokuActionSection
				clearQuestionAction={() => clearQuestionAction()}
				clearAnswerAction={() => clearAnswerAction()}
				sampleQuestionAction={() => sampleQuestionAction()}
			/>

			<div style={{ display: "inline-block", margin: "1em", verticalAlign: "top" }}>
				<table id="sudoku-table">
					<tbody>
						{Array(9)
							.fill(null)
							.map((e, row) => (
								<tr key={row + 1}>
									{Array(9)
										.fill(null)
										.map((o, col) => (
											<DigitCell
												key={row * 9 + col + 1}
												selected={
													(row === selectedRow && col === selectedCol) ||
													(sudokuAnswerCellValues[row][col] &&
														sudokuAnswerCellValues[row][col] ===
															sudokuAnswerCellValues[selectedRow][selectedCol])
												}
												answer={
													!sudokuQuestionCellValues[row][col] &&
													!!sudokuAnswerCellValues[row][col]
												}
												highlighted={
													row === selectedRow ||
													col === selectedCol ||
													getNonetIndexByRowCol(row, col) ===
														getNonetIndexByRowCol(selectedRow, selectedCol)
												}
												onClick={() => {
													setSelectedRow(row);
													setSelectedCol(col);
												}}
												style={{ position: "relative" }}
											>
												{sudokuQuestionCellValues[row][col] ||
													sudokuAnswerCellValues[row][col] || <>&nbsp;</>}
												<div className="possible-values">
													{sudokuPossibleAnswerCellValues[row][col].join("")}
												</div>
											</DigitCell>
										))}
								</tr>
							))}
					</tbody>
				</table>
			</div>

			<div style={{ display: "inline-block", margin: "1em", verticalAlign: "top" }}>
				<table id="digits-table" className={configData.answerMode ? "answer-mode" : "question-mode"}>
					<tbody>
						{Array(3)
							.fill(null)
							.map((e, i) => (
								<tr key={i + 1}>
									{Array(3)
										.fill(null)
										.map((o, j) => i * 3 + j + 1)
										.map((v) => (
											<DigitCell
												key={v}
												onClick={() => {
													// cannot overwrite question with answer
													if (
														configData.answerMode &&
														!!sudokuQuestionCellValues[selectedRow][selectedCol]
													) {
														return;
													}

													setSudokuAnswerCellValues(
														getNewMultiDimensionalArrayWithUpdatedValue(
															sudokuAnswerCellValues,
															v ===
																(sudokuQuestionCellValues[selectedRow][selectedCol] ||
																	sudokuAnswerCellValues[selectedRow][selectedCol])
																? null
																: v,
															selectedRow,
															selectedCol
														)
													);

													if (!configData.answerMode) {
														setSudokuQuestionCellValues(
															getNewMultiDimensionalArrayWithUpdatedValue(
																sudokuQuestionCellValues,
																v === sudokuQuestionCellValues[selectedRow][selectedCol]
																	? null
																	: v,
																selectedRow,
																selectedCol
															)
														);
													}
												}}
											>
												{v}
											</DigitCell>
										))}
								</tr>
							))}
					</tbody>
				</table>
			</div>

			<div className="config">
				<pre>{JSON.stringify(configData, null, 4)}</pre>
				<pre>
					Question:
					{JSON.stringify(
						sudokuQuestionCellValues.map((e) => e.map((o) => (o ? o : "_")).join(" ")),
						null,
						4
					)}
				</pre>
				<pre>
					Answer:
					{JSON.stringify(
						sudokuAnswerCellValues.map((e) => e.map((o) => (o ? o : "_")).join(" ")),
						null,
						4
					)}
				</pre>
			</div>
		</>
	);
}

export default Sudoku;
export { Sudoku };
