import chunk from "lodash/chunk";
import { CombinationUtils } from "./";
import {
	removeValuesInMultiDimensionalArray,
	getNonetIndexByRowCol,
	getNonetIndexByColChunk,
	getNonetIndexByRowChunk,
	getColCellValuesByCol,
	getRowCellValuesByRow,
	getRowOtherCellValuesByRowCol,
	getRowOtherCellValuesByRowChunk,
	getRowOtherCellValuesByColChunk,
	getColOtherCellValuesByRowCol,
	getNonetOtherCellValuesByRowChunk,
	getNonetOtherCellValuesByColChunk,
	getNonetOtherCellValuesByRowCol,
	getPossibleAnswerValuesByRowChunk,
	getPossibleAnswerValuesByColChunk,
	getRowPossibleAnswerValuesByRow,
	getColPossibleAnswerValuesByCol,
	getNonetPossibleAnswerValuesByNonetIndex,
} from "./SudokuUtils";

function solveSudoku(sudokuAnswerCellValues, sudokuPossibleAnswerCellValues) {
	let newSudokuPossibleAnswerCellValues = JSON.parse(JSON.stringify(sudokuPossibleAnswerCellValues));

	// eliminate possible values for empty cells if the row, col or nonet are filled with the values
	for (let row = 0; row < 9; row++) {
		for (let col = 0; col < 9; col++) {
			if (sudokuAnswerCellValues[row][col]) {
				newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
					newSudokuPossibleAnswerCellValues,
					Array(9)
						.fill(null)
						.map((e, i) => i + 1),
					row,
					col
				);
				continue;
			}
			const otherCellValuesRow = getRowOtherCellValuesByRowCol(sudokuAnswerCellValues, row, col).filter((e) => e);
			const otherCellValuesCol = getColOtherCellValuesByRowCol(sudokuAnswerCellValues, row, col).filter((e) => e);
			const otherCellValuesNonet = getNonetOtherCellValuesByRowCol(sudokuAnswerCellValues, row, col).filter(
				(e) => e
			);
			const possibleValuesToRemove = Array.from(
				new Set([...otherCellValuesRow, ...otherCellValuesCol, ...otherCellValuesNonet])
			);

			newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
				newSudokuPossibleAnswerCellValues,
				possibleValuesToRemove,
				row,
				col
			);
		}
	}

	// if the other cells of this row chunk can fill all the blanks in other chunks of the same row,
	// the possible values in this row chunk can only appear in this row chunk but not other row chunks of the same row
	for (let row = 0; row < 9; row++) {
		for (let chunk = 0; chunk < 3; chunk++) {
			const nonetOtherCellValues = getNonetOtherCellValuesByRowChunk(sudokuAnswerCellValues, row, chunk).filter(
				(e) => e
			);
			const rowOtherChunkCellValues = getRowOtherCellValuesByRowChunk(sudokuAnswerCellValues, row, chunk).filter(
				(e) => e
			);

			const rowChunkAnswerPossibleValues = getPossibleAnswerValuesByRowChunk(
				newSudokuPossibleAnswerCellValues,
				row,
				chunk
			);

			if (
				Array.from(new Set([...nonetOtherCellValues, ...rowOtherChunkCellValues])).length === 6 &&
				rowChunkAnswerPossibleValues.length === 3
			) {
				for (let c = 0; c < 9; c++) {
					if (Math.floor(c / 3) !== chunk) {
						newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
							newSudokuPossibleAnswerCellValues,
							Array(9)
								.fill(null)
								.map((e, i) => i + 1)
								.filter(
									(v) => !nonetOtherCellValues.includes(v) && !rowOtherChunkCellValues.includes(v)
								),
							row,
							c
						);
					}
				}
			}
		}
	}

	// if the other cells of this col chunk can fill all the blanks in other chunks of the same col,
	// the possible values in this col chunk can only appear in this col chunk but not other col chunks of the same col
	for (let col = 0; col < 9; col++) {
		for (let chunk = 0; chunk < 3; chunk++) {
			const nonetOtherCellValues = getNonetOtherCellValuesByColChunk(sudokuAnswerCellValues, col, chunk).filter(
				(e) => e
			);
			const colOtherChunkCellValues = getRowOtherCellValuesByColChunk(sudokuAnswerCellValues, col, chunk).filter(
				(e) => e
			);

			const colChunkAnswerPossibleValues = getPossibleAnswerValuesByColChunk(
				newSudokuPossibleAnswerCellValues,
				col,
				chunk
			);

			if (
				Array.from(new Set([...nonetOtherCellValues, ...colOtherChunkCellValues])).length === 6 &&
				colChunkAnswerPossibleValues.length === 3
			) {
				for (let r = 0; r < 9; r++) {
					if (Math.floor(r / 3) !== chunk) {
						newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
							newSudokuPossibleAnswerCellValues,
							Array(9)
								.fill(null)
								.map((e, i) => i + 1)
								.filter(
									(v) => !nonetOtherCellValues.includes(v) && !colOtherChunkCellValues.includes(v)
								),
							r,
							col
						);
					}
				}
			}
		}
	}

	for (let num = 1; num <= 9; num++) {
		// if this row chunk is the only chunk of the same row that has this num as possible value,
		// other row chunks in the same nonet should not have this num as possible value
		for (let row = 0; row < 9; row++) {
			const rowChunkValues = chunk(getRowCellValuesByRow(newSudokuPossibleAnswerCellValues, row), 3).map((e) =>
				Array.from(new Set(e.flatMap((e) => e)))
			);

			if (rowChunkValues.filter((o) => o.includes(num)).length !== 1) {
				continue;
			}

			const chunkIndex = rowChunkValues.map((o, i) => (o.includes(num) ? i : null)).filter((o) => o)[0];

			for (let r = 0; r < 9; r++) {
				for (let c = 0; c < 9; c++) {
					if (r !== row && getNonetIndexByRowCol(r, c) === getNonetIndexByRowChunk(row, chunkIndex)) {
						newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
							newSudokuPossibleAnswerCellValues,
							[num],
							r,
							c
						);
					}
				}
			}
		}

		// if this col chunk is the only chunk of the same col that has this num as possible value,
		// other col chunks in the same nonet should not have this num as possible value
		for (let col = 0; col < 9; col++) {
			const colChunkValues = chunk(getColCellValuesByCol(newSudokuPossibleAnswerCellValues, col), 3).map((e) =>
				Array.from(new Set(e.flatMap((e) => e)))
			);

			if (colChunkValues.filter((o) => o.includes(num)).length !== 1) {
				continue;
			}

			const chunkIndex = colChunkValues.map((o, i) => (o.includes(num) ? i : null)).filter((o) => o)[0];

			for (let r = 0; r < 9; r++) {
				for (let c = 0; c < 9; c++) {
					if (c !== col && getNonetIndexByRowCol(r, c) === getNonetIndexByColChunk(col, chunkIndex)) {
						newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
							newSudokuPossibleAnswerCellValues,
							[num],
							r,
							c
						);
					}
				}
			}
		}

		for (let nonet = 0; nonet < 9; nonet++) {
			const nonetPossibleAnswerValues = getNonetPossibleAnswerValuesByNonetIndex(
				newSudokuPossibleAnswerCellValues,
				nonet
			)
				.map(({ possibleAnswers, row, col }) => ({
					possible: possibleAnswers.includes(num),
					row,
					col,
				}))
				.filter(({ possible }) => possible);

			if (!nonetPossibleAnswerValues.length || nonetPossibleAnswerValues.length > 3) {
				continue;
			}

			// if this row chunk is the only row chunk in the same nonet that have this num as possible value,
			// other chunks in the same row should not have this num as possible value
			if (
				nonetPossibleAnswerValues.length ===
				nonetPossibleAnswerValues.filter((e) => e.row === nonetPossibleAnswerValues[0].row).length
			) {
				for (let c = 0; c < 9; c++) {
					if (getNonetIndexByRowCol(nonetPossibleAnswerValues[0].row, c) !== nonet) {
						newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
							newSudokuPossibleAnswerCellValues,
							[num],
							nonetPossibleAnswerValues[0].row,
							c
						);
					}
				}
			}

			// if this col chunk is the only col chunk in the same nonet that have this num as possible value,
			// other chunks in the same col should not have this num as possible value
			if (
				nonetPossibleAnswerValues.length ===
				nonetPossibleAnswerValues.filter((e) => e.col === nonetPossibleAnswerValues[0].col).length
			) {
				for (let r = 0; r < 9; r++) {
					if (getNonetIndexByRowCol(r, nonetPossibleAnswerValues[0].col) !== nonet) {
						newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
							newSudokuPossibleAnswerCellValues,
							[num],
							r,
							nonetPossibleAnswerValues[0].col
						);
					}
				}
			}
		}

		/* -------------------------------------------------- */

		// if this cell is the only one in the row that has this num as possible value,
		// this cell's possible values should only include this num but not the other nums
		for (let row = 0; row < 9; row++) {
			const numPossibleAnswers = newSudokuPossibleAnswerCellValues[row]
				.map((col) => col.includes(num))
				.map((v, c) => ({ col: c, possible: v }))
				.filter(({ possible }) => possible);

			if (numPossibleAnswers.length === 1) {
				newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
					newSudokuPossibleAnswerCellValues,
					Array(9)
						.fill(null)
						.map((e, i) => i + 1)
						.filter((v) => v !== num),
					row,
					numPossibleAnswers[0].col
				);
			}
		}

		// if this cell is the only one in the col that has this num as possible value,
		// this cell's possible values should only include this num but not the other nums
		for (let col = 0; col < 9; col++) {
			const numPossibleAnswers = newSudokuPossibleAnswerCellValues
				.map((rowArr) => rowArr[col])
				.map((row) => row.includes(num))
				.map((v, r) => ({ row: r, possible: v }))
				.filter(({ possible }) => possible);

			if (numPossibleAnswers.length === 1) {
				newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
					newSudokuPossibleAnswerCellValues,
					Array(9)
						.fill(null)
						.map((e, i) => i + 1)
						.filter((v) => v !== num),
					numPossibleAnswers[0].row,
					col
				);
			}
		}

		// if this cell is the only one in the nonet that has this num as possible value,
		// this cell's possible values should only include this num but not the other nums
		for (let nonet = 0; nonet < 9; nonet++) {
			const numPossibleAnswers = getNonetPossibleAnswerValuesByNonetIndex(
				newSudokuPossibleAnswerCellValues,
				nonet
			)
				.map(({ possibleAnswers, row, col }) => ({ possible: possibleAnswers.includes(num), row, col }))
				.filter(({ possible }) => possible);

			if (numPossibleAnswers.length === 1) {
				newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
					newSudokuPossibleAnswerCellValues,
					Array(9)
						.fill(null)
						.map((e, i) => i + 1)
						.filter((v) => v !== num),
					numPossibleAnswers[0].row,
					numPossibleAnswers[0].col
				);
			}
		}
	}

	/* -------------------------------------------------- */

	const eliminateOtherCellPossibleValuesWithCombinations = (possibleAnswerValues) => {
		const combinations = CombinationUtils.powerSet(possibleAnswerValues).filter(
			(e) => e.length >= 2 && e.length <= 7
		);

		for (const combination of combinations) {
			if (combination.length === possibleAnswerValues.length) {
				continue;
			}

			const possibleAnswers = Array.from(new Set(combination.flatMap((v) => v.possibleAnswers)));
			if (possibleAnswers.length !== combination.length) {
				continue;
			}

			for (const otherCell of possibleAnswerValues) {
				if (combination.some((v) => v.row === otherCell.row && v.col === otherCell.col)) {
					continue;
				}

				newSudokuPossibleAnswerCellValues = removeValuesInMultiDimensionalArray(
					newSudokuPossibleAnswerCellValues,
					possibleAnswers,
					otherCell.row,
					otherCell.col
				);
			}
		}
	};

	// if the number of some cells' possible values in the row is equal to the number of these cells,
	// these values can only be in these cells and other cells in the same row should not have these possible values
	for (let row = 0; row < 9; row++) {
		const rowPossibleAnswerValues = getRowPossibleAnswerValuesByRow(newSudokuPossibleAnswerCellValues, row).filter(
			(v) => v.possibleAnswers.length > 0
		);

		eliminateOtherCellPossibleValuesWithCombinations(rowPossibleAnswerValues);
	}

	// if the number of some cells' possible values in the col is equal to the number of these cells,
	// these values can only be in these cells and other cells in the same col should not have these possible values
	for (let col = 0; col < 9; col++) {
		const colPossibleAnswerValues = getColPossibleAnswerValuesByCol(newSudokuPossibleAnswerCellValues, col).filter(
			(v) => v.possibleAnswers.length > 0
		);

		eliminateOtherCellPossibleValuesWithCombinations(colPossibleAnswerValues);
	}

	// if the number of some cells' possible values in the nonet is equal to the number of these cells,
	// these values can only be in these cells and other cells in the same nonet should not have these possible values
	for (let nonet = 0; nonet < 9; nonet++) {
		const nonetPossibleAnswerValues = getNonetPossibleAnswerValuesByNonetIndex(
			newSudokuPossibleAnswerCellValues,
			nonet
		).filter((v) => v.possibleAnswers.length > 0);

		eliminateOtherCellPossibleValuesWithCombinations(nonetPossibleAnswerValues);
	}

	return newSudokuPossibleAnswerCellValues;
}

const functions = {
	solveSudoku,
};
export default functions;
export { solveSudoku };
