import { Button, ButtonGroup } from "@mui/material";

function SudokuActionSection(props) {
	const { clearQuestionAction, clearAnswerAction, sampleQuestionAction } = props;

	return (
		<div style={{ display: "inline-block", margin: "1em", verticalAlign: "top" }}>
			<ButtonGroup orientation="vertical">
				<Button variant="contained" color="primary" onClick={() => sampleQuestionAction()}>
					Sample Q
				</Button>

				<Button variant="contained" color="primary" onClick={() => clearQuestionAction()}>
					Clear Q
				</Button>

				<Button variant="contained" color="primary" onClick={() => clearAnswerAction()}>
					Clear A
				</Button>
			</ButtonGroup>
		</div>
	);
}

export default SudokuActionSection;
export { SudokuActionSection };
