import chunk from "lodash/chunk";

const removeValuesInMultiDimensionalArray = (mdArr, valuesToRemove, ...dimensions) => {
	let arrRef = mdArr;
	for (let i = 0; i < dimensions.length - 1; i++) {
		arrRef = arrRef[dimensions[i]];
	}
	arrRef[dimensions[dimensions.length - 1]] = arrRef[dimensions[dimensions.length - 1]].filter(
		(e) => !valuesToRemove.includes(e)
	);

	return mdArr;
};

const getNonetIndexByRowCol = (row, col) => Math.floor(row / 3) * 3 + Math.floor(col / 3);
const getNonetIndexByColChunk = (col, chunk) => getNonetIndexByRowCol(chunk * 3, col);
const getNonetIndexByRowChunk = (row, chunk) => getNonetIndexByRowCol(row, chunk * 3);

const getColCellValuesByCol = (sudoku2DArray, col) => sudoku2DArray.map((rowArr) => rowArr[col]);
const getRowCellValuesByRow = (sudoku2DArray, row) => sudoku2DArray[row];

const getRowOtherCellValuesByRowCol = (sudoku2DArray, row, col) => sudoku2DArray[row].filter((v, c) => c !== col);
const getRowOtherCellValuesByRowChunk = (sudoku2DArray, row, chunk) =>
	sudoku2DArray[row].filter((v, c) => getNonetIndexByRowCol(row, c) !== getNonetIndexByRowChunk(row, chunk));
const getRowOtherCellValuesByColChunk = (sudoku2DArray, col, chunk) =>
	sudoku2DArray
		.map((rowArr) => rowArr[col])
		.filter((v, r) => getNonetIndexByRowCol(r, col) !== getNonetIndexByColChunk(col, chunk));

const getColOtherCellValuesByRowCol = (sudoku2DArray, row, col) =>
	sudoku2DArray.map((rowArr) => rowArr[col]).filter((v, i) => i !== row);

const getNonetOtherCellValuesByRowChunk = (sudoku2DArray, row, chunk) =>
	sudoku2DArray.flatMap((rowArr, r) =>
		rowArr
			.map((colCell, c) =>
				getNonetIndexByRowCol(r, c) !== getNonetIndexByRowChunk(row, chunk)
					? undefined
					: r !== row
					? colCell
					: null
			)
			.filter((e) => e !== undefined)
	);
const getNonetOtherCellValuesByColChunk = (sudoku2DArray, col, chunk) =>
	sudoku2DArray.flatMap((rowArr, r) =>
		rowArr
			.map((colCell, c) =>
				getNonetIndexByRowCol(r, c) !== getNonetIndexByColChunk(col, chunk)
					? undefined
					: c !== col
					? colCell
					: null
			)
			.filter((e) => e !== undefined)
	);
const getNonetOtherCellValuesByRowCol = (sudoku2DArray, row, col) =>
	sudoku2DArray.flatMap((rowArr, r) =>
		rowArr
			.map((colCell, c) =>
				getNonetIndexByRowCol(r, c) !== getNonetIndexByRowCol(row, col)
					? undefined
					: r !== row || c !== col
					? colCell
					: null
			)
			.filter((e) => e !== undefined)
	);

const getPossibleAnswerValuesByRowChunk = (sudokuPossibleAnswerValues, row, chunkIndex) =>
	Array.from(new Set(chunk(sudokuPossibleAnswerValues[row], 3)[chunkIndex].flatMap((e) => e)));
const getPossibleAnswerValuesByColChunk = (sudokuPossibleAnswerValues, col, chunkIndex) =>
	Array.from(
		new Set(
			chunk(
				sudokuPossibleAnswerValues.map((rowArr) => rowArr[col]),
				3
			)[chunkIndex].flatMap((e) => e)
		)
	);
const getRowPossibleAnswerValuesByRow = (sudokuPossibleAnswerValues, row) =>
	sudokuPossibleAnswerValues[row].map((colCell, c) => ({ possibleAnswers: colCell, row, col: c }));
const getColPossibleAnswerValuesByCol = (sudokuPossibleAnswerValues, col) =>
	sudokuPossibleAnswerValues
		.map((rowArr, r) => rowArr[col])
		.map((rowCell, r) => ({ possibleAnswers: rowCell, row: r, col }));
const getNonetPossibleAnswerValuesByNonetIndex = (sudokuPossibleAnswerValues, nonet) =>
	sudokuPossibleAnswerValues.flatMap((rowArr, r) =>
		rowArr
			.map((colCell, c) =>
				getNonetIndexByRowCol(r, c) === nonet ? { possibleAnswers: colCell, row: r, col: c } : null
			)
			.filter((e) => e)
	);

const functions = {
	removeValuesInMultiDimensionalArray,
	getNonetIndexByRowCol,
	getNonetIndexByColChunk,
	getNonetIndexByRowChunk,
	getColCellValuesByCol,
	getRowCellValuesByRow,
	getRowOtherCellValuesByRowCol,
	getRowOtherCellValuesByRowChunk,
	getRowOtherCellValuesByColChunk,
	getColOtherCellValuesByRowCol,
	getNonetOtherCellValuesByRowChunk,
	getNonetOtherCellValuesByColChunk,
	getNonetOtherCellValuesByRowCol,
	getPossibleAnswerValuesByRowChunk,
	getPossibleAnswerValuesByColChunk,
	getRowPossibleAnswerValuesByRow,
	getColPossibleAnswerValuesByCol,
	getNonetPossibleAnswerValuesByNonetIndex,
};
export default functions;
export {
	removeValuesInMultiDimensionalArray,
	getNonetIndexByRowCol,
	getNonetIndexByColChunk,
	getNonetIndexByRowChunk,
	getColCellValuesByCol,
	getRowCellValuesByRow,
	getRowOtherCellValuesByRowCol,
	getRowOtherCellValuesByRowChunk,
	getRowOtherCellValuesByColChunk,
	getColOtherCellValuesByRowCol,
	getNonetOtherCellValuesByRowChunk,
	getNonetOtherCellValuesByColChunk,
	getNonetOtherCellValuesByRowCol,
	getPossibleAnswerValuesByRowChunk,
	getPossibleAnswerValuesByColChunk,
	getRowPossibleAnswerValuesByRow,
	getColPossibleAnswerValuesByCol,
	getNonetPossibleAnswerValuesByNonetIndex,
};
