function addCombosForNewItem(item, template) {
	const itemsToAdd = template.map(function (i) {
		return [...(i.length ? i : [i]), item];
	});

	return [...template, [item], ...itemsToAdd];
}

function powerSet(list) {
	let result = [];

	for (const item of list) {
		result = addCombosForNewItem(item, result);
	}

	return result;
}

const functions = {
	powerSet,
};
export default functions;
export { powerSet };
